/* eslint-disable array-callback-return */
import { useEffect, useState } from "react";
import { useContext } from "react";
import { useNavigate } from "react-router-dom";

//Components
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import "../../index.css";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';


//dependencies
import Context from "../context/UserContext";
import {ListAutores} from "../../services/autor";
import AutorModal from "./autoresModal";
import { useModal } from "../../hooks/useModal";



const AutorList = ({autores}) => {
  const history = useNavigate();

  const [show, handleShow, handleClose] = useModal(false);
  const [id, setId] = useState("");
  const [currentItem, setCurrentItem] = useState({});
  const [listAutores, setAutores] = useState(autores);
  const initialFormData = {autores:""};
  const [newData, setFormData] = useState(initialFormData);
  const { user } = useContext(Context);

  const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(0),
        marginBottom: theme.spacing(10),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    title1: {
      textAlign: "center",
      color: "white"
    }
  }));

  
  const editar = async (item) => {
    setId(item.id);
    setCurrentItem(item);
    setFormData({ autor: item.autor });
    handleShow(true)
  };

  const registrar = async () => {
    setId(null);
    setCurrentItem({});
    setFormData({autor:""});
    handleShow(true)
  };


  const getAutores = async () => {
    try {
      const res = await ListAutores();
      const data = await res;
      setAutores(data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (user && user.is_superuser) {
      getAutores();
      setId(null)
    } else {
    history("/loginuser");
      
    }
    
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const classes = useStyles();
  
  if (user) {
    
    return (
      <Container> 
        
        <div className={classes.paper}>
        <h1 className="mt-4 text-center">Autores</h1>
        </div>
        <Stack  alignItems="center">
          <Button 
          variant="contained" 
          color="success"
          type="submit"
          onClick={()=>registrar()}>
            Registrar Autor
          </Button>
        </Stack>
        <br/><br/> <br/>     
        <div>
          <section className="content">
          <div className="table-responsive table-condensed table-sm tabla">
            <table
              className="table table-hover"
              id="dataTableData"
              name="dataTableData"
              style={{ fontSize: 11, textAlign: "center" }}
            >
              <thead className={classes.title1}>
                <tr>
                  <th style={{ display: "none" }}>Id</th>
                  <th>Codigo</th>
                  <th>Autor</th>
                  <th></th>
                </tr>
              </thead>
              <tbody style={{ backgroundColor: "white" }}>
                {listAutores.length === 0 ? (
                  <tr>
                    <td colSpan={11}>No hay datos</td>
                  </tr>
                ) : (
                  listAutores.map((Autor, index) => (
                    <tr key={index}>
                      <td style={{ display: "none" }}>{Autor.id}</td>
                      <td>{Autor.id}</td>
                      <td>{Autor.autor}</td>
                      <td>
                        <button
                          className="btn btn-outline-primary p-1 btn-sm float-right"
                          type="submit"
                          onClick={()=>editar(Autor)}
                        >
                          editar
                        </button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </section>
        </div>
        <AutorModal
          handleClose={handleClose}
          show={show}
          autor_id ={id}
          currentItem={currentItem}
          setAutores = {setAutores}
          setFormData = {setFormData}
          newData={newData}
        ></AutorModal>
      </Container>
    );
  }
  return (
    <p style={{ fontSize: "25px" }}>Inicia sesión para ver todos los videos!</p>
  );
};

export default AutorList;
