import { useEffect, useState } from "react";
import {  useContext } from "react";

//dependencies
import ListAutores from "../especialidades/listAutores"
import * as AutorServer from "../../services/autor";
import VideoLoadingComponent from "../videos/videoLoading";
import Context from "../context/UserContext";

//components
import { Container } from "@material-ui/core";

const  Principal = () =>{
    const AutoresLoading = VideoLoadingComponent(ListAutores);
    const { user } = useContext(Context);

    const [appState, setAppState] = useState({
      loading: true,
      autores: null,
    });   
    
    useEffect(() => {
        AutorServer.ListAutores().then((res) => {
        const allAutores = res;
        setAppState({ loading: false, autores: allAutores });
      });
    }, [setAppState]);
  if (user) {
    return (
      <Container>
        <div className="App">
          <AutoresLoading isLoading={appState.loading} autores={appState.autores} />
        </div>
      </Container>
    );
  }
  return (
    <p style={{ fontSize: "25px" }}>Inicia sesión para ver todos los videos!</p>
  );
}

export default Principal;
