import axiosInstance from "../axios";

const API_URL = "https://sccotview.com/api/autores/";

/**
 * Función para consultar todas las autores registradas en la base de datos.
 * @returns Data enviada desde el backend
 */
  export const ListAutores = async () => {
  const response = await axiosInstance.get(API_URL);
  console.log(response);
  
  if (response.status === 200) {
    return await response.data;
  }
};

export const RegisterAutor = async (newAutor) => {
  const response = await axiosInstance.post(API_URL, newAutor);
  console.log(response);

  if (response.status === 200 || response.status === 201) { // Aceptar código 201
    return response.data;
  }

  return null; // Devolver null si la respuesta no es la esperada
};

export const getAutor = async (autor) => {
  try {
    const response = await axiosInstance.get(`${API_URL}${autor}`);
    if (response.status === 200) {
      return response.data; // No es necesario el `await`
    }
  } catch (error) {
    console.error("❌ Error al obtener el autor:", error);
    return null; // Devuelve `null` en caso de error
  }
};


export const updateAutor = async(id,updAutor) =>{
  const response = await axiosInstance.patch(API_URL+id+"/",updAutor);
  if (response.status === 200) {
      return response.data;
  }
}